<template>
  <v-sheet class="overflow-hidden" style="position: relative">
    <v-toolbar color="success" class="white--text">
      <v-btn icon @click="$store.dispatch('toPrevRouter')">
        <v-icon large class="white--text icon_border">mdi-arrow-left</v-icon>
      </v-btn>

      <v-toolbar-title></v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn
        icon
        class="white--text"
        x-large
        @click="submit"
        :loading="loading"
        :disabled="loading"
      >
        
      </v-btn>
    </v-toolbar>

    <v-card
      color="grey lighten-3"
      elevation="0"
      class="overflow-y-auto"
      raised
      style="height: 90vh"
    >
      <v-alert color="red darken-2" class="mx-3" dark v-if="$store.state.error">
        {{ $store.state.error }}
      </v-alert>

      <!--  -->
      <v-list-item class="mt-2" style="background: white">
        <v-list-item-content>
          <v-list-item-titl class="text-h6"></v-list-item-titl>
          <v-card-text class="pa-0">
            <v-menu
              :nudge-right="40"
              v-model="menu2"
              autofocus
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  ref="date"
                  class="pa-0"
                  append-icon="mdi-calendar"
                  placeholder=""
                  readonly
                  v-bind="attrs"
                  v-on="on"
                  hide-details="auto"
                  v-model="comment.date"
                  :rules="[() => !!comment.date || '']"
                ></v-text-field>
              </template>

              <v-date-picker
                v-model="comment.date"
                @input="menu2 = false"
                :max="
                  new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
                    .toISOString()
                    .substr(0, 10) + 1
                "
              ></v-date-picker>
            </v-menu>
          </v-card-text>
        </v-list-item-content>
      </v-list-item>
      <!--  -->
      <v-list-item class="mt-2" style="background: white">
        <v-list-item-content>
          <v-list-item-title class="text-h6"></v-list-item-title>

          <v-card-text class="pa-0 mt-1">
            <v-rating
              color="yellow darken-3"
              dense
              hover
              background-color="grey lighten-1"
              x-large
              v-model="comment.star"
              class="rating-padding"
            ></v-rating>

            <span class="text-caption error--text">{{ errorMessage }}</span>
          </v-card-text>
        </v-list-item-content>
      </v-list-item>
      <!--  -->
      <v-list-item class="mt-2" style="background: white">
        <v-list-item-content>
          <v-list-item-title class="text-h6"
            ><span class="float-right">{{ showDifficult }}</span></v-list-item-title
          >
          <v-card-text class="pa-0">
            <v-slider
              :tick-labels="[1, 2, 3, 4, 5]"
              step="25"
              ticks="always"
              color="success"
              class="slider-success"
              thumb-fill-color="success"
              tick-size="2"
              v-model="difficulty"
            ></v-slider>
          </v-card-text>
        </v-list-item-content>
      </v-list-item>
      <!--  -->
      <v-list-item class="mt-2" style="background: white">
        <v-list-item-content>
          <v-list-item-title class="text-h6"
            ><span class="float-right">{{ showBeauty }}</span></v-list-item-title
          >
          <v-card-text class="pa-0">
            <v-slider
              :tick-labels="[1, 2, 3, 4, 5]"
              step="25"
              ticks="always"
              color="success"
              class="slider-success"
              thumb-fill-color="success"
              tick-size="2"
              v-model="beauty"
            ></v-slider>
          </v-card-text>
        </v-list-item-content>
      </v-list-item>
      <!--  -->
      <v-list-item class="mt-2" style="background: white">
        <v-list-item-content>
          <v-list-item-title class="text-h6"></v-list-item-title>
          <v-row class="d-flex align-center py-0">
            <v-col class="py-0">
              <v-text-field
                ref="hour"
                hide-details="auto"
                placeholder=""
                v-model="comment.hour"
                :rules="[() => !!comment.hour || '']"
              ></v-text-field>
            </v-col>

            <v-col class="py-0"
              ><v-text-field
                ref="min"
                hide-details="auto"
                placeholder=""
                v-model="comment.min"
                :rules="[() => !!comment.min || '']"
              ></v-text-field>
            </v-col>
          </v-row>

          <v-list-item-title class="text-h6 mt-3"></v-list-item-title>

          <v-card-text class="pa-0">
            <v-textarea
              ref="content"
              clearable
              clear-icon="mdi-close-circle"
              placeholder="......"
              v-model="comment.content"
              :rules="[() => !!comment.content || '']"
            ></v-textarea>
          </v-card-text>

          <!-- <v-card-action>
            15
            <v-btn block color="success" outlined x-large class="mt-3"></v-btn>
          </v-card-action> -->
        </v-list-item-content>
      </v-list-item>
    </v-card>
  </v-sheet>
</template>

<script>
export default {
  name: "SuccessPage",
  data() {
    return {
      menu2: false,
      loading: false,
      formHasErrors: false,
      errorMessage: "",
      difficulty: 50,
      beauty: 50,
      comment: {
        user_id: null,
        trail_id: null,
        duration: null,
        hour: null,
        min: null,
        difficulty: null,
        beauty: null,
      },
    };
  },
  methods: {
    async submit() {
      this.loading = true;
      this.formHasErrors = false;
      Object.keys(this.form).forEach((f) => {
        if (!this.form[f]) this.formHasErrors = true;
        if (!this.$refs[f].validate(true)) this.formHasErrors = true;
      });
      if (!this.comment.star) {
        this.formHasErrors = true;
        this.errorMessage = "";
      }
      if (!this.formHasErrors) {
        this.prepareData();
        await this.postComment();
      }
      this.loading = false;
    },
    async postComment() {
      await this.$axios.postApi("/api/comment", this.comment).then((res) => {
        if (!res) return;
        this.$store.dispatch("toPrevRouter");
      });
    },
    prepareData() {
      this.comment.user_id = this.$cookies.get("user_Id");
      this.comment.trail_id = this.$route.params.id;
      this.comment.duration = parseInt(this.comment.hour * 60) + parseInt(this.comment.min);
      this.comment.difficulty = this.difficulty ? this.difficulty / 25 + 1 : null;
      this.comment.beauty = this.beauty ? this.beauty / 25 + 1 : null;
    },
  },
  computed: {
    form() {
      return {
        date: this.comment.date,
        hour: this.comment.hour,
        min: this.comment.min,
        content: this.comment.content,
      };
    },
    showDifficult() {
      switch (this.difficulty) {
        case 0:
          return "";
        case 25:
          return "";
        case 50:
          return "";
        case 75:
          return "";
        case 100:
          return "";
      }
    },
    showBeauty() {
      switch (this.beauty) {
        case 0:
          return "";
        case 25:
          return "";
        case 50:
          return "";
        case 75:
          return "";
        case 100:
          return "";
      }
    },
  },
  watch: {
    "comment.star"(nv) {
      if (this.formHasErrors && nv) this.errorMessage = "";
    },
  },
};
</script>

